.slick-login {
  width: 480px;
  height: 155px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -250px;
  margin-top: -200px;
}

.login-card {
  box-shadow:
    4.3px 2.6px 2.1px rgba(0, 0, 0, 0.056),
    10.4px 6.2px 5.1px rgba(0, 0, 0, 0.081),
    19.5px 11.6px 9.5px rgba(0, 0, 0, 0.1),
    34.8px 20.8px 17px rgba(0, 0, 0, 0.119),
    65.2px 38.9px 31.8px rgba(0, 0, 0, 0.144),
    156px 93px 76px rgba(0, 0, 0, 0.2)
;
}


.btn {
  padding: 0.5em 1.5em;
  font-size: 16px;
  border: none;
  background: #6285f8;
  color: white;
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.btn:hover {
  color: rgb(255, 255, 255);
}

.btn::before,.btn::after {
  content: "";
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background: rgba(128, 128, 128, 0.096);
  backdrop-filter: blur(10px);
  z-index: -1;
  transition: all 0.4s;
}

.btn::after {
  z-index: -2;
  transition: all 0.4s;
}

.btn:hover::before {
  background: rgb(0, 0, 0, 0.315);
  border-radius: 10px;
}

.btn:hover::after {
  background: linear-gradient(to right, #cb6ce6, #38b6ff);
  border-radius: 10px;
  transform: scale(1.07, 1.2) rotateX(180deg);
}
